<template>
    <yd-layout class="box_login">
        <div class="box_top">
            <img src="../assets/logo_03.png" alt="">
            <div class="logo_name">风险风机管控与隐患排查治理体系系统</div>
        </div>
        <div class="box_content">
            <div class="login_title">
                <div class="login_txt">账号登陆</div>
                <div class="login_line"></div>
            </div>
            <div class="login_content">
                <div class="login_user">
                    <div class="login_img">
                        <img src="../assets/username_03.png" alt="">
                    </div>
                    <input type="tel" placeholder="请输入您的账号" v-model="username" :max="11">
                </div>
                <div class="login_user">
                    <div class="login_img">
                        <img src="../assets/password_03.png" alt="">
                    </div>
                    <input type="password" placeholder="请输入您的密码" v-model="password">
                </div>
            </div>
            <div style="padding: 0.5rem 1.5rem;">
                <yd-button class="box_button" color="#fff" bgcolor="#1D91E6" size="large"  type="primary" @click.native="onPressLogin">登 录</yd-button>
            </div>
        </div>
        <!--<div>登录页面</div>-->
        <!--<yd-button size="large" type="primary" @click.native="onPressLogin">登录</yd-button>-->
    </yd-layout>
</template>

<script>
    import {api_login} from '../components/APIKeys'
    import {mapState} from 'vuex'

    export default {
        name: 'Login',
        components: {},
        props: [],
        data: function () {
            return {
                username:null,
                password:'',
            }
        },
        computed: mapState({
            openid: ({user}) => user.user.openid,
        }),
        created: function () {

        },
        mounted: function () {

        },

        activated: function () {

        },

        methods: {
            //点击登录
            onPressLogin() {
                // if(this.username == '' || this.username.length != 11){
                //     this.$showError('请输入正确的账号');
                //     return false;
                // }else if(this.password == ''){
                //     this.$showError('请输入密码');
                //     return false;
                // }
                this.$showDialog();
                let jsonData = {
                    username:this.username,
                    password:this.password,
                };
                this.$post(api_login, jsonData).then(res=>{
                    this.$showDialog(false);
                    if(res.errno == 0){
                        let time = parseInt(res.data.SYSJ);
                        if(time <= 30){
                            this.$dialog.confirm({
                                title: '提示',
                                mes: '您的账号还有' + time + '天过期',
                                opts: () => {
                                    this.$showSuccess('登录成功');
                                    this.$login(res.data);
                                }
                            });
                        }else{
                            this.$showSuccess('登录成功');
                            this.$login(res.data);
                        }
                        // console.log('这个账号的token',res.data.token);
                        // window.localStorage.setItem('token',res.data.token);
                        // this.$showSuccess('登录成功');
                        // this.$login(res.data);
                        // console.log('执行跳转')
                        // this.$pushRoute('Root');
                    }else{
                        this.$showError(res.errmsg);
                    }
                })
            },
        }
    }
</script>

<style scoped>
    .box_login{
        background: url("../assets/loginbg.png") 0 0 no-repeat;
        background-size:100%;
    }
    .box_top{
        display: flex;
        flex-direction:column;
        align-items: center;
        justify-content: center;
        padding-top: 1rem;
    }
    .box_top img{
        width: 1.5rem;
        height: 1.5rem;
    }
    .logo_name{
        color: #fff;
        font-size: .36rem;
        margin-top: .3rem;
        font-family: '微软雅黑';
    }
    .box_content{
        margin: .3rem;
        padding: .3rem .24rem;
        background: #fff;
        border-radius:10px;
        border: 1px solid #d9d9d9;
    }
    .login_title{
        padding:.3rem;
        display: flex;
        flex-direction:column;
        align-items: center;
        justify-content: center;
    }
    .login_txt{
        font-size: .34rem;
        color: #000;
        text-align: center;
    }
    .login_line{
        width: 1.7rem;
        height: 0.07rem;
        background: #3989e2;
        margin-top: .08rem;
    }
    .login_content{
        padding: .24rem .15rem;
        margin-top: .4rem;
    }
    .login_user{
        background: #f8f8f8;
        border-radius:5px;
        display: flex;
        align-items: center;
        padding: .24rem .3rem ;
        margin-bottom: .3rem;
    }
    .login_img{
        width: .4rem;
        height: .4rem;
        /*margin-top:0.1rem;*/
    }
    .login_img img{
        width: 100%;
        height: 100%;
    }
    .login_user input{
        flex: 1;
        border: 0;
        margin-left: .2rem;
        /*background: #f8f8f8;*/
    }
    .box_button{
        border-radius:200px;
        font-size: .36rem;
        font-weight: 600;
        font-family: '微软雅黑'
    }
</style>



