Vue.prototype.$showDialog = function (show = true, text = '加载中...') {
    if (show) {
        this.$dialog.loading.open(text);
    }
    else {
        this.$dialog.loading.close();
    }
};
Vue.prototype.$showError = function (text = '') {
    this.$dialog.toast({
        mes: text,
        timeout: 1500,
        icon: 'error',
    });
};

Vue.prototype.$showSuccess = function (text = '', timeout = 1500) {
    this.$dialog.toast({
        mes: text,
        timeout,
        icon: 'success',
    });
};


