<template>
    <div class="container">
        <GridItem :key="'grid_item_'+index"
                  v-for="item,index in data"
                  :label="item.label"
                  :image="item.image"
                  style="width: 25%">
        </GridItem>
    </div>
</template>

<script>

    import GridItem from './GridItem.vue'
    export default {
        name: 'GridView',
        components: {GridItem},
        props: {
            data: {
                type: Array,
                default: [],
            },
            imageStyle: {
                type: Object,
                default: {},
            },
        },
        data: function () {
            return {}
        },
        computed: {},
        created: function () {

        },
        methods: {}
    }
</script>

<style scoped>
    .container {
        display: flex;
        /*flex-direction: row;*/
        /*position: absolute;*/
        flex-wrap: wrap;
    }

    .item {
        display: flex;
        flex-direction: column;
        width: 25%;
        align-items: center;
        justify-content: center;
        padding: 0.3rem 0rem;
    }

    .item-image {
        width: 0.4rem;
        height: 0.4rem;
    }

    .item-label {
        margin-top: 0.1rem;
        font-size: 0.3rem;
    }
</style>



