<template>
    <yd-layout class="home">
        <navBar title="首页" slot="navbar"></navBar>
        <yd-grids-group :rows="3" item-height="2rem">
            <yd-grids-item v-for="m,mx in menus" @click.native="onPressMenu(m)" :key="'menu_' + mx" v-if="m.isShow">
                <div slot="else" class="box_div">
                    <img :src="m.image" class="box_img">
                    <p style="padding: 5px;color:#666666;">{{m.title}}</p>
                </div>
            </yd-grids-item>
        </yd-grids-group>
        <div class="box_bottom">
            <div style="flex: 1;"></div>
            <div>版本号：v1.0.1</div>
            <div style="flex: 1;"></div>
        </div>
    </yd-layout>
</template>

<script>
    import {mapState} from 'vuex'
    import {} from '../components/APIKeys'
    import {isWX, isAndroid, isIOS} from '../components/tools'

    export default {
        name: 'Home',
        components: {},
        props: [],
        data: function () {
            return {
                menus: [
                    {title: 'NFC', image: require('../assets/NFC.png'), ident: 'nfc',isShow:true},
                    {title: '二维码巡检', image: require('../assets/saomiao.png'), ident: 'er_wei_ma',isShow:true},
                    {title: '企业巡检点', image: require('../assets/xunjiandian.png'), ident: 'qi_ye',isShow:true},
                    {title: '班次信息', image:require('../assets/banci.png'), ident: 'ban_ci',isShow:true},
                    {title: '巡检记录', image: require('../assets/jilu.png'), ident: 'xun_jian',isShow:true},
                    {title: '隐患处理', image:require('../assets/yinhuan.png'), ident: 'yin_huan',isShow:true},
                    {title: '安全资料', image:require('../assets/ziliao.png'), ident: 'zi_liao',isShow:true},
                    {title: '统计信息', image:require('../assets/tongji.png'), ident: 'tong_ji',isShow:true},
                    {title: '随手拍', image:require('../assets/camaro.png'), ident: 'sui_shou_pai',isShow:true},
                    {title: '随手拍隐患记录', image:require('../assets/yinhuanjilu.png'), ident: 'sui_shou_pai_list',isShow:true},
                    {title: '退出', image:require('../assets/tuichu.png'), ident: 'tui_chu',isShow:true},
                ]
            }
        },
        computed: mapState({
            // userInfo: ({user}) => user.userInfo,
            openid: ({user}) => user.user.openid,
        }),
        created: function () {

        },
        mounted: function () {
            this.isShowNFC();
        },
        methods: {
            //判断是在app || 微信,是否显示NFC
            isShowNFC(){
                let self = this;
                if(isAndroid){
                    self.menus[0].isShow = true;
                    // console.log('这是app')
                }else{
                    self.menus[0].isShow = false;
                    // console.log('这不是app')
                }
                // console.log('NFC*****************',this.menus[0].isShow)
            },

            onPressMenu(m) {
                console.log('点击菜单', m);
                switch (m.ident) {
                    // 二维码
                    case 'er_wei_ma': {
                        // this.$pushRoute('xunjiandian_kaishi');
                        if (isIOS || isAndroid) {
                            let self = this;
                            native.barcode_scan( function (barcode) {
                                window.localStorage.setItem('checkLocationId',barcode);
                                self.$pushRoute('xunjiandian_kaishi');
                            });
                        } else if (isWX) {

                        }
                    }
                        break;

                    case 'nfc': {
                        // this.$pushRoute('xunjiandian_kaishi');
                        if (isAndroid) {
                            let self = this;
                            native.nfc_reader( function (barcode) {
                                window.localStorage.setItem('checkLocationId',barcode);
                                self.$pushRoute('xunjiandian_kaishi');
                            });
                        } else {
                            this.$showError("请使用安卓APP运行此功能");
                        }
                    }
                        break;
                    // 企业巡检点
                    case 'qi_ye': {
                        this.$pushRoute('qy_xunjiandian');
                    }
                        break;
                    // 班次信息
                    case 'ban_ci': {
                        this.$pushRoute('banci');
                    }
                        break;
                    //巡检记录
                    case 'xun_jian': {
                        this.$pushRoute('CheckList');
                    }
                        break;
                    //隐患处理
                    case 'yin_huan': {
                        this.$pushRoute('DangerList');
                    }
                        break;
                    //安全资料
                    case 'zi_liao': {
                        this.$pushRoute('ziliao');
                    }
                        break;
                    //统计信息
                    case 'tong_ji': {
                        this.$pushRoute('StatisticsData');
                    }
                        break;
                    //统计信息
                    case 'sui_shou_pai': {
                        this.$pushRoute('Camera');
                    }
                        break;
                    //统计信息
                    case 'sui_shou_pai_list': {
                        this.$pushRoute('CameraList');
                    }
                        break;
                    //退出
                    case 'tui_chu': {
                        this.$dialog.confirm({
                            title: '确定要退出吗?',
                            mes: '',
                            opts: () => {
                                // this.$dialog.toast({mes: '你点了确定', timeout: 1000});
                                this.$logout();
                            }
                        });
                    }
                        break;

                    // 测试
                    case 'test': {
                        this.$pushRoute('test-lightbox');
                    }
                        break;

                    default:
                        break;
                }
            }
        },
        activated() {

        }
    }
</script>

<style scoped>
    .box_div{
        text-align: center;
    }
    .box_img{
        height: 1rem;
    }
    .box_bottom{
        position: absolute;
        bottom: 0.1rem;
        color:gray;
        font-size: .28rem;
        display: flex;
        width: 100%;
    }

</style>
<style>
    .home .yd-grids-3{
        padding-top: .24rem;
    }
    .home .yd-grids-item:after{
        display: none;
    }
    .home .yd-grids-3 .yd-grids-item:not(:nth-child(3n)):before{
        display: none;
    }
   .yd-confirm-ft>a.primary{
        color: #028be6 !important;
    }
</style>



