import wx from "weixin-js-sdk";
import {api_getWxConfig} from "./APIKeys";
import VueJsonp from "vue-jsonp";

/**
 * @author yyc
 */
export default {
    install: function (Vue, options) {

        Vue.use(VueJsonp);
        const getWxConfig = () => {
            let currentUrl = encodeURIComponent(window.location.href.split("#")[0]);

            //请求配置
            Vue.prototype.$post(api_getWxConfig, {requestUrl: currentUrl}).then((res) => {
                //let {data} = res;
                // config信息验证后会执行ready方法，所有接口调用都必须在config接口获得结果之后，config是一个客户端的异步操作

                // console.log('getWxConfig', res);

                wx.config({
                    debug: false, // 开启调试模式,开发时可以开启
                    appId: res.appId,   // 必填，公众号的唯一标识   由接口返回
                    timestamp: res.timestamp, // 必填，生成签名的时间戳 由接口返回
                    nonceStr: res.nonceStr,    // 必填，生成签名的随机串 由接口返回
                    signature: res.signature,   // 必填，签名 由接口返回
                    jsApiList: ['getLocation', 'openLocation', 'chooseImage', 'previewImage', 'uploadImage', 'downloadImage', 'getLocalImgData'] // 此处填你所用到的方法
                });
            });
        };

        /**
         * 微信位置接口
         *
         * @callback locationCallback
         * @param {string} address - 地址
         * @param {(string|number)} latitude - 纬度
         * @param {(string|number)} longitude - 经度
         */

        /***
         * 微信获取位置。
         *
         * @param {locationCallback} callback - 回调
         */
        Vue.prototype.$wxGetLocation = function (callback) {

            getWxConfig();
            wx.ready(function () {
                // config信息验证后会执行ready方法，所有接口调用都必须在config接口获得结果之后，
                // config是一个客户端的异步操作，所以如果需要在页面加载时就调用相关接口，则须把相关接口放在ready函数中调用来确保正确执行。
                // 对于用户触发时才调用的接口，则可以直接调用，不需要放在ready函数中。
                wx.checkJsApi({
                    jsApiList: [
                        'getLocation',
                        'openLocation',
                    ],
                    success: function (res) {
                    }
                });
                wx.getLocation({
                    type: 'gcj02', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
                    success: function (res) {
                        let latitude = res.latitude; // 纬度，浮点数，范围为90 ~ -90
                        let longitude = res.longitude; // 经度，浮点数，范围为180 ~ -180。
                        let speed = res.speed; // 速度，以米/每秒计
                        let accuracy = res.accuracy; // 位置精度

                        Vue.jsonp('https://apis.map.qq.com/ws/geocoder/v1/', {
                            location: `${latitude},${longitude}`,
                            coord_type: 5,
                            key: '62FBZ-XD7LJ-BP4FA-KDYFI-7NAEE-L7FZJ',
                            output: 'jsonp',
                            callback: 'calllocation'
                        }).then(json => {
                            let {address, formatted_addresses} = json.result;

                            if (typeof callback === 'function') {
                                callback(formatted_addresses.recommend, latitude, longitude);
                            }
                        });
                    }
                });
            });

            wx.error(function (res) {
                // config信息验证失败会执行error函数，如签名过期导致验证失败
                // 具体错误信息可以打开config的debug模式查看，也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
            });
        };

        /***
         * 打开地图。
         * @param {string} address - 地址
         * @param {(string|number)} latitude - 纬度
         * @param {(string|number)} longitude - 经度
         */
        Vue.prototype.$wxOpenLocation = function (address, latitude, longitude) {
            getWxConfig();
            wx.ready(() => {
                wx.checkJsApi({
                    jsApiList: ['openLocation',
                    ],
                    success: (res) => {
                    }
                });

                //使用微信内置地图查看位置接口
                wx.openLocation({
                    latitude, // 纬度，浮点数，范围为90 ~ -90
                    longitude, // 经度，浮点数，范围为180 ~ -180。
                    name: '我的位置', // 位置名
                    address: address, // 地址详情说明
                    scale: 28, // 地图缩放级别,整形值,范围从1~28。默认为最大
                    infoUrl: '' // 在查看位置界面底部显示的超链接,可点击跳转（测试好像不可用）
                });
            });

            wx.error((res) => {
                // config信息验证失败会执行error函数，如签名过期导致验证失败
                // 具体错误信息可以打开config的debug模式查看，也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
            });
        };


        /**
         * 微信拍照回调接口
         *
         * @callback imageCallback
         * @param {string} localId - 返回选定照片的本地ID，可以作为img标签的src属性显示图片
         * @param {string} serverId - 返回图片的服务器端ID （备注：上传图片有效期3天，可用微信多媒体接口下载图片到自己的服务器，此处获得的 serverId 即 media_id。）
         * @param {?string} localData - 图片的base64数据，可以用img标签显示。&nbsp;<b>注意：仅在iOS WKWebview 下提供，用于兼容 iOS WKWebview 不支持 localId 直接显示图片的问题。<br/>详细参见<a href=https://mp.weixin.qq.com/wiki?t=resource/res_main&id=mp1483682025_enmey>iOS WKWebview 网页开发适配指南</a></b>
         */

        /**
         * 微信拍照
         *
         * @param {imageCallback} cb - 微信拍照回调接口
         */
        Vue.prototype.$wxTakePhoto = function (cb) {
            getWxConfig();

            // let images = {
            //     localIds: [],
            //     serverId: [],
            //     data: [],
            // };

            //拍照或从手机相册中选图接口
            const wxChooseImage = () => wx.chooseImage({
                count: 1, // 默认9
                sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
                sourceType: ['camera'], // 可以指定来源是相册'album'还是相机，默认二者都有
                success: (res) => {
                    //返回选定照片的本地ID列表，localId可以作为img标签的src属性显示图片
                    // if (typeof imageSrcCallback === 'function') {
                    //    imageSrcCallback(res.localIds);
                    // }

                    wxUploadImage(res.localIds);
                },
            });

            //上传图片接口
            const wxUploadImage = (localIds) => {
                localIds.forEach((localId) => wx.uploadImage({
                    localId, // 需要上传的图片的本地ID，由chooseImage接口获得
                    isShowProgressTips: 1, // 默认为1，显示进度提示
                    success: (res) => {
                        let serverId = res.serverId; // 返回图片的服务器端ID

                        // if (typeof imageSrcCallback === 'function') {
                        //     imageSrcCallback(localId);
                        // }
                        //
                        // if (typeof imageServerIdCallback == 'function') {
                        //     imageServerIdCallback(serverId);
                        // }

                        if (window.__wxjs_is_wkwebview) {
                            wx.getLocalImgData({
                                localId: localId, // 图片的localID
                                success: (result) => {
                                    let localData = result.localData;
                                    //console.log("getLocalImgData", localData);

                                    // if (typeof imageBase64Callback === 'function') {
                                    //     imageBase64Callback(localData);
                                    // }

                                    if (typeof cb === 'function') {
                                        cb(localId, serverId, localData);
                                    }
                                }
                            });
                        } else {
                            if (typeof cb === 'function') {
                                cb(localId, serverId, null);
                            }
                        }
                    }
                }));
            };


            //下载图片接口
            const wxDownloadImage = (serverId, success) => wx.downloadImage({
                serverId, // 需要下载的图片的服务器端ID，由uploadImage接口获得
                isShowProgressTips: 1, // 默认为1，显示进度提示
                success: (res) => {
                    let localId = res.localId; // 返回图片下载后的本地ID
                    success(localId);
                }
            });

            wx.ready(() => {
                wx.checkJsApi({
                    jsApiList: [
                        'chooseImage',
                        'previewImage',
                        'uploadImage',
                        'downloadImage',
                        'getLocalImgData'
                    ],
                    success: (res) => {
                        if (res.checkResult.chooseImage == false) {
                            alert('你的微信版本太低，不支持微信JS接口，请升级到最新的微信版本！');
                            return;
                        } else {
                            wxChooseImage();
                        }
                    }
                });
            });


            wx.error(function (res) {
                // config信息验证失败会执行error函数，如签名过期导致验证失败，具体错误信息可以打开config的debug模式查看，也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
                alert("验证失败，请重试！");
            });
        };
    }
}



