<template>
        <header class="yd-navbar navbar-bottom-line-color" :class="classes"
                :style="{backgroundColor: bgcolor, height: height}">

            <slot name="left">
                <div class="yd-navbar-item" v-if="isIOS">
                    <div @click="$pop()" v-if="back">
                        <yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
                    </div>
                </div>
            </slot>

            <div class="yd-navbar-center-box" :style="titleStyle">
                <div class="yd-navbar-center" :style="titleCenter">
                    <slot name="center">
                    <span class="yd-navbar-center-title"
                          :style="titleText">{{title}}</span>
                    </slot>
                </div>
            </div>

            <div class="yd-navbar-item">
                <slot name="right"></slot>
            </div>
        </header>
</template>

<script>
    import {isIOS, isAndroid} from '../tools'

    export default {
        name: 'navigation-bar',
        components: {},
        props: {
            title: String,
            fixed: Boolean,
            back: Boolean,
            bgcolor: {
                default: '#FFF'
            },
            color: {
                default: '#fff'
            },
//            height: {
//                validator(value) {
//                    return /^(\.|\d+\.)?\d+(px|rem)$/.test(value);
//                },
//                default: '1rem'
//            }
        },
        data: function () {
            return {
                isIOS, isAndroid
            }
        },
        computed: {
            classes() {
                return this.fixed ? 'yd-navbar-fixed' : '';
            },
            height() {
                return isIOS ? '44px' : '48px';
            },
            fontSize() {
                return isIOS ? '.34rem' : '.4rem';
            },
            titleStyle() {
                let style = {
                    color: this.color,
                };

                if (isIOS) {
                    style.height = '44px';
                    style.width = '50%';
                    style.marginLeft = '25%';
                }

                else if (isAndroid) {
                    style.height = '48px';
                    style.width = '75%';
                    style.marginLeft = '0%';
                }

                return style;
            },
            titleText() {
                return {
                    color: this.color,
                    fontSize: this.fontSize,
                    fontWeight: isIOS ? 'bold' : 'normal',
                }
            },
            titleCenter() {
                let style = {
                    justifyContent: 'center',
                };

                if (isAndroid) {
                    style.justifyContent = 'flex-start';
                    style.marginLeft = '15px';
                }
                return style;
            },
        },
        created: function () {

        },
        methods: {}
    }
</script>

<style scoped>
    .ios-status-bar {
        height: 20px;
    }
</style>



