const routes = [
    { //隐患记录
        path: '/danger',
        name: 'DangerList',
        component: resolve => require(['@/modules/danger/DangerList.vue'], resolve),
        meta: {
            title: '隐患处理'
        }
    },
    { //隐患整改
        path: '/danger/correct',
        name: 'DangerCorrect',
        component: resolve => require(['@/modules/danger/DangerCorrect.vue'], resolve),
        meta: {
            title: '隐患整改'
        }
    },
    { //隐患复查
        path: '/danger/recheck',
        name: 'DangerRecheck',
        component: resolve => require(['@/modules/danger/DangerRecheck.vue'], resolve),
        meta: {
            title: '隐患复查'
        }
    },

];

export default routes;



