<template>
    <yd-navbar :title="title" :bgcolor="theme" :color="tintColor" v-if="$config('showNavBar')">
        <div @click="$pop()" slot="left" v-if="back">
            <yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
        </div>
    </yd-navbar>
</template>

<script>
    export default {
        name: 'NavBar',
        components: {},
        props: {
            title: {
                type: String,
                default: '标题',
            },
            back: {
                type: Boolean,
                default: true
            }
        },
        data: function () {
            return {
                theme: this.$config('theme'),
                tintColor: this.$config('themeTintColor'),
            }
        },
        computed: {
            // userInfo: ({user}) => user.userInfo,
        },
        created: function () {

        },
        methods: {
            popRoute: function () {

            }
        }
    }
</script>

<style scoped>
    .yd-navbar:after {
        border-bottom:0px !important;
    }
</style>



