const routes = [
    { //随手拍
        path: '/camera',
        name: 'Camera',
        component: resolve => require(['@/modules/camera/Camera.vue'], resolve),
        meta: {
            title: '随手拍'
        }
    },
    { //随手拍隐患记录
        path: '/cameralist',
        name: 'CameraList',
        component: resolve => require(['@/modules/camera/CameraList.vue'], resolve),
        meta: {
            title: '随手拍隐患记录'
        }
    },
    { //随手拍隐患整改
        path: '/cameralist/correct',
        name: 'CameraCorrect',
        component: resolve => require(['@/modules/camera/CameraCorrect.vue'], resolve),
        meta: {
            title: '随手拍隐患整改'
        }
    },
    { //随手拍隐患复查
        path: '/cameralist/recheck',
        name: 'CameraRecheck',
        component: resolve => require(['@/modules/camera/CameraRecheck.vue'], resolve),
        meta: {
            title: '随手拍隐患复查'
        }
    },

];

export default routes;



