<template>
    <div class="item" @click="onPress">
        <img name="image" class="item-image" :src="image" :style="imageStyle">
        <span name="label" class="item-label" :style="labelStyle">{{label}}</span>
    </div>
</template>

<script>
    export default {
        name: 'GridItem',
        components: {},
        props: {
            label: {
                type: String,
                default: '',
            },
            labelStyle: {
                type: Object,
                default: {color: '#000'},
            },
            image: {
                type: String,
                default: '',
            },
            imageSize: {
                type: String,
                default: '',
            },
            imageStyle: {
                type: Object,
                default: {},
            },
            data: {
                type: [String, Object, Number],
                default: '',
            }
        },
        data: function () {
            return {}
        },
        computed: {
            // userInfo: ({user}) => user.userInfo,
//            imageStyle: function () {
//                if (this.imageSize.length) {
//                    return {width: this.imageSize, height: this.imageSize}
//                }
//            }
        },
        created: function () {

        },
        mounted: function () {

        },
        methods: {
            onPress: function () {
                this.$emit('onPressGridItem', this.data);
            }
        }
    }
</script>

<style scoped>
    .item {
        display: flex;
        flex-direction: column;
        width: 25%;
        align-items: center;
        justify-content: center;
        padding: 0.3rem 0rem;
    }

    .item-image {
        width: 0.4rem;
        height: 0.4rem;
    }

    .item-label {
        margin-top: 0.1rem;
        font-size: 0.3rem;
    }
</style>



