import Vue from 'vue'
import Router from 'vue-router'
// import Navigation from 'vue-navigation'
import Root from '../modules/Home.vue' //微信登录及自动登录
import Login from '../modules/Login.vue' //登录页面
import test from './test' //测试
import check from './check' //巡检记录
import danger from './danger' //隐患处理
import statistics from './statistics' //统计数据
import camera from './camera' //随手拍
import banci from './banci' //班次信息
import ziliao from './ziliao' //安全资料
import ziliaocon from './ziliaocon' //安全资料大数据库
import xunjiandian from './xunjiandian' //巡检点
import xunjiandian_kaishi from './xunjiandian_kaishi'  //巡检点
import HistoryList from './HistoryList' //历史记录
import qy_xunjiandian from './qy_xunjiandian' //企业巡检点
import jianchatiaomu from './jianchatiaomu' //检查条目

Vue.use(Router);

export default new Router({
    routes: [
        { //自动登录
            path: '/',
            name: 'Root',
            component: Root,
            meta: {
                title: '首页'
            }
        },
        { //登录
            path: '/login',
            name: 'Login',
            component: Login,
            meta: {
                title: '登录'
            }
        },
        ...test,
        ...check,
        ...danger,
        ...statistics,
        ...camera,
        ...banci,
        ...ziliao,
        ...ziliaocon,
        ...xunjiandian,
        ...xunjiandian_kaishi,
        ...HistoryList,
        ...qy_xunjiandian,
        ...jianchatiaomu,
    ]
})



