<template>
    <div class="form">
        <div class="content">
            <slot name="left"></slot>
            <slot name="right"></slot>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'FormItem',
        components: {},
        props: [],
        data: function () {
            return {}
        },
        computed: {
            // userInfo: ({user}) => user.userInfo,
        },
        created: function () {

        },
        methods: {}
    }
</script>

<style scoped>
    .form {
        display: flex;
        min-height: 1.0rem;
        align-items: center;
        position: relative;
        padding-left: 0.3rem;
        padding-right: 0.3rem;
    }

    .content {
        display: flex;
        justify-content: space-between;
        flex-grow: 1;
        align-items: center;
    }

    .form:not(:last-child):after {
        content: "";
        z-index: 10;
        bottom: 0;
        left: 0;
        width: 100%;
        position: absolute;
        border-bottom: 1px solid #d9d9d9;
        -webkit-transform: scaleY(.5);
        transform: scaleY(.5);
        -webkit-transform-origin: 0 0;
        transform-origin: 0 0;
        margin-left: 0.24rem;
    }
</style>



